<template>
  <div id="app" data-theme="light">
    <transition :name="transitionName">
      <router-view></router-view>
      <!-- <v-touch
        @swipeleft="toName != 'message' && isExist && onSwipeLeft()"
        @swiperight="toName != 'message' && onSwipeRight()"
        class="v-touch"
      >
        <router-view></router-view>
      </v-touch> -->
    </transition>
    <tabbar class="tabbar" />
  </div>
</template>

<script>
export default {
  name: "App",
  data() {
    return {
      toName: "",
      index: "",
      isExist: false,
      tabsList: ["home", "square", "publish", "message", "mine"],
      transitionName: "", // slide-left
      tel: "18846427434",
      userList: [
        { id: 1, name: "wss", tel: "18846427434" },
        { id: 2, name: "wss", tel: "176111681081" },
      ],
    };
  },
  watch: {
    $route(to, from) {
      if (from.meta.back == undefined) {
        this.transitionName = "";
      } else if (from.meta.back) {
        this.transitionName = "slide-right";
      } else {
        this.transitionName = "slide-left";
      }
      this.toName = to.name;
      let flag = this.tabsList.findIndex((item) => {
        return to.name == item;
      });
      if (flag > -1) {
        this.isExist = true;
        this.index = flag;
      } else {
        this.isExist = false;
        this.index = "";
      }
    },
  },
  mounted() {
    // 当前账号在用户列表里到login，不在的话quickLogin
    localStorage.setItem("tel", this.tel);
    localStorage.setItem("checked", false);
    let flag = this.userList.findIndex((item) => {
      return this.tel == item.tel;
    });
    if (flag > -1) {
      this.$router.push("/login");
    } else {
      this.$router.push("/quickLogin");
    }
  },
  methods: {
    onSwipeLeft() {
      if (this.index + 1 <= 4 && this.index + 1 >= 1) {
        let name = "/" + this.tabsList[this.index + 1];
        this.$router.push(name);
        this.transitionName = "slide-left";
      }
    },
    onSwipeRight() {
      if (this.isExist && this.index - 1 >= 0) {
        let name = "/" + this.tabsList[this.index - 1];
        this.$router.push(name);
      } else {
        this.$router.go(-1);
      }
      this.transitionName = "slide-right";
    },
  },
};
</script>

<style scoped>
.v-touch {
  touch-action: pan-y !important;
  overflow: auto;
}
.v-touch::-webkit-scrollbar {
  display: none;
}
#app {
  position: relative;
}
.tabbar {
  position: absolute;
  bottom: 0;
}
.slide-right-enter-active,
.slide-right-leave-active,
.slide-left-enter-active,
.slide-left-leave-active {
  /* will-change属性可以提前通知浏览器我们要对元素做什么动画，这样浏览器可以提前准备合适的优化设置 */
  will-change: transform;
  transition: all ease 0.4s;
  -webkit-transition: all ease 0.4s;
  position: absolute;
  width: 89.4vw;
  left: 0;
}
.user,
.chatContent.slide-right-enter-active,
.user,
.chatContent.slide-right-leave-active,
.user,
.chatContent.slide-left-enter-active,
.user,
.chatContent.slide-left-leave-active {
  width: 100vw;
}
.slide-right-enter {
  transform: translateX(-100%);
  -webkit-transform: translateX(-100%);
}
.slide-right-leave-active {
  transform: translateX(100%);
  -webkit-transform: translateX(100%);
}
.slide-left-enter {
  transform: translateX(100%);
  -webkit-transform: translateX(100%);
}
.slide-left-leave-active {
  transform: translateX(-100%);
  -webkit-transform: translateX(-100%);
}
</style>
