<template>
  <div>
    <van-swipe-cell v-for="(item, index) in list" :key="index">
      <div class="comItem" @click="isClick && handleClick(item)">
        <div class="comItem-left">
          <div class="avatar">
            <img
              src="../../assets/user/avatar.svg"
              width="50"
              height="50"
              style="border-radius: 50%"
            />
          </div>
          <div class="name">
            <p v-if="item.name">{{ item.name }}</p>
            <p v-if="item.message">{{ item.message }}</p>
            <p v-if="item.address">{{ item.address }}</p>
          </div>
        </div>
        <div v-if="item.time" class="time">{{ item.time }}</div>
        <div class="distance" v-if="item.distance">
          <van-icon name="location-o" />距离{{ item.distance }}km
        </div>
      </div>
      <template #right v-if="isSlide">
        <van-button
          square
          type="danger"
          :text="text"
          @click="operation(item, index)"
        />
      </template>
    </van-swipe-cell>
  </div>
</template>

<script>
export default {
  props: {
    list: {
      type: Array,
      default: () => {
        [];
      },
    },
    isSlide: {
      type: Boolean,
      default: false,
    },
    isClick: {
      type: Boolean,
      default: false,
    },
    text: {
      type: String,
      default: "",
    },
  },
  methods: {
    operation(item, index) {
      this.$emit("changeList", item, index);
    },
    handleClick(item) {
      this.$emit("handleClick", item);
    },
  },
};
</script>

