  margin-left: 5px;
}
.name p {
  margin: 0;
}
.name p:first-child {
  font-size: 13px;
  margin-bottom: 5px;
}
.name p:last-child,
.distance {
  font-size: 12px;
  color: #999;
  margin-right: 5px;
}
.time {
  margin-top: -10px;
