<template>
  <!-- :class="isDark ? 'isDark' : ''" -->
  <div
    :class="isDark ? 'isDark' : ''"
    class="defaultHome"
    v-if="
      $route.path == '/home' ||
      $route.path == '/square' ||
      $route.path == '/publish' ||
      $route.path == '/message' ||
      $route.path == '/mine' ||
      $route.path == '/discover'
    "
  >
    <van-tabbar v-model="active">
      <van-tabbar-item
        icon="wap-home-o"
        name="home"
        :to="{ path: '/home' }"
      ></van-tabbar-item>
      <van-tabbar-item
        icon="award-o"
        name="square"
        :to="{ path: '/square' }"
      ></van-tabbar-item>
      <van-tabbar-item
        icon="add-o"
        name="publish"
        :to="{ path: '/publish' }"
      ></van-tabbar-item>
      <van-tabbar-item
        icon="comment-circle-o"
        name="message"
        :to="{ path: '/message' }"
      ></van-tabbar-item>
      <van-tabbar-item
        icon="user-o"
        name="mine"
        :to="{ path: '/mine' }"
      ></van-tabbar-item>
    </van-tabbar>
  </div>
</template>

<script>
export default {
  props: {
    // activeIndex: {
    //   type: String,
    //   default: "square",
    // },
    // isDark: {
    //   type: Boolean,
    //   default: false,
    // },
  },
  data() {
    return {
      isDark: false,
      active: 0,
      tabbarList: [
        "/home",
        "/square",
        "/publish",
        "/message",
        "/mine",
        "/discover",
      ],
      // active: this.activeIndex,
    };
  },
  watch: {
    $route(to) {
      this.activeTab(to.path);
    },
  },
  methods: {
    activeTab(path) {
      let index = this.tabbarList.map((item) => item).indexOf(path);
      if (path == "/discover") {
        this.active = 0;
      } else if (index > -1) {
        this.active = index;
      }
      if (path == "/home") {
        this.isDark = true;
      } else {
        this.isDark = false;
      }
    },
  },
};
</script>

<style scoped>
.isDark >>> .van-tabbar-item {
  background: #1d1d2b !important;
  margin-bottom: -1px !important;
}

[class*="van-hairline"]::after {
  border: none;
}
.isDark >>> .van-tabbar--fixed {
  background: #1d1d2b !important;
}
.isDark >>> .van-tabbar-item--active {
  color: #8c9cf7;
}
>>> .van-tabbar-item--active van-tabbar-item__icon {
  color: #6e86ff;
}
</style>